.links__title {
  margin-left: 450px;
  margin-bottom: 50px;
  text-align: left;
  font-weight: 100;
  color: white;
}

.social-links {
  display: flex;
  justify-content: center;
  list-style: none;
}

.social-links li {
  margin-right: 50px;
  padding: 0 20px;
  display: inline;
  transition: all 0.5s;
}

.social-links li:hover {
  background-color: rgb(210, 234, 255);
  color: black;
  transform: scale(1.2);
}

.social-links a {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  font-weight: 200;
  text-decoration: none;
  color: white;
  transition: all 0.5s;
}

.social-links a:hover {
  color: black;
}

@media (max-width: 768px) {
  .links__title {
    margin-left: 20px;
    margin-bottom: 20px;
    text-align: center;
  }

  .social-links {
    flex-wrap: wrap;
    justify-content: center;
  }

  .social-links li {
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 0 10px;
  }
}
